<template>
    <div class="picwrap">
        <el-upload
            :action="uploadUrl"
            list-type="picture"
            :headers="headers"
            name="fileContent"
            :data="params"
            :file-list="imgageList"
            :before-upload="beforeUpload"
            :on-success="handleSuccess"
            :on-remove="handleRemove"
            :on-error="imgUploadError"
        >
            <!-- :limit="5" 限制上传数量   -->
            <!-- :on-exceed="handleExceed"  文件超出个数限制时的钩子 -->
            <el-button
                size="small"
                type="primary"
            >点击上传</el-button>
            <label
                slot="tip"
                class="el-upload__tip"
            >&nbsp;&nbsp;图片支持jpg、jpeg、png等</label>
        </el-upload>
    </div>
</template>
 
<script>
export default {
  name: "uploadpic",
  data() {
    return {
      uploadUrl: "http://hn216.api.yesapi.cn/?s=App.CDN.UploadImgByBase64",
      headers: {},
      params: {},
      imgageList: []
    };
  },
  mounted() {
    this.headers = {
      "Access-Control-Allow-Origin": "*",
      "Content-Type": "multipart/form-data" //请求头
    };
    this.params = {
      return_data: 0,
      app_key: "0D01C4CA26367BA195F8402DACC184C7",
      sign: "6C2ACED8A83871EB87590FFADCE41DB6",
      file_name: "",
      file: "",
      file_type: ""
    };
  },
  methods: {
    //图片上传成功
    handleSuccess(res, file, fileList) {
      this.imgageList.push({
        name: file.name,
        path: file.response.data,
        url: file.url,
        sort: ""
      });
    },
    //移除图片
    handleRemove(file, fileList) {},
    //文件上传之前调用 做一些拦截限制
    beforeUpload(file) {
      const isJPG = file.type === "image/jpeg" || "image/jpg" || "image/png";
      console.log(file, "file");
      this.params.file_type = file.type;
      this.params.file_name = file.name;
      if (!isJPG) {
        this.$Message.error("上传的图片只能是jpg、jpeg、png格式!");
      }
      return isJPG;
    },
    //文件超出个数限制时
    handleExceed(file, fileList) {
      this.$Message.error("上传图片超出限制！");
    },
    //图片上传失败
    imgUploadError(file, fileList) {
      this.$Message.error("上传图片失败！");
    }
  }
};
</script>




