<template>
  <div class="picbox">
    <!-- <Affix :offset-top="0">
      <Uploadpic></Uploadpic>
    </Affix> -->
    <ul
      v-for="(item,num) in baseData.photoBookModule.photoBookData.allBookData"
      :key="num"
      v-show="item.modulePermission"
    >
      <li
        class="reveal-bottom"
        v-for="(i,index) in item.bookData"
        :key="index"
      >
        <a href="/">
          <i>
            <img
              :src="i.imageSrc+i.backgroundImageName+'.jpg'"
              @click.stop.prevent="select_photo(i.router,i.photoBookId,i.classPhotoBookId)"
              :alt="i.title"
            />
          </i>
          <div class="picinfo">
            <h3>{{i.title}}</h3>
            <span>{{i.desc}}</span>
          </div>
        </a>
      </li>
    </ul>
  </div>
</template>

<script>
import Uploadpic from "@/components/common/upload/uploadpic";
import scrollReveal from "scrollreveal";
export default {
  components: {
    Uploadpic,
  },
  data() {
    return {
      scrollReveal: scrollReveal(),
    };
  },
  mounted() {
    this.scrollReveal.reveal(".reveal-bottom", {
      // 动画的时长
      duration: 1000,
      // 延迟时间
      delay: 500,
      // 动画开始的位置，'bottom', 'left', 'top', 'right'
      origin: "bottom",
      // 回滚的时候是否再次触发动画
      reset: false,
      // 在移动端是否使用动画
      mobile: false,
      // 滚动的距离，单位可以用%，rem等
      distance: "20px",
      // 其他可用的动画效果
      opacity: 0.001,
      easing: "linear",
      scale: 0.9,
    });
  },
  methods: {
    select_photo(router, id, classId) {
      this.$router.push({ path: router, query: { id: id, classId: classId } });
    },
  },
};
</script>

<style lang="scss" src="@/assets/scss/photo.scss" scoped>
</style>


